    name: 'radioBar',
    props: ['isCollapse'],
    data() {
      return {
        vCharts: {
          num: [
            "/vcharts/vline",
            "/vcharts/vhistogram",
            "/vcharts/vbar",
            "/vcharts/vpie",
            "/vcharts/vring",
            "/vcharts/vwaterfull",
            "/vcharts/vfunnel",
            "/vcharts/vradar",
            "/vcharts/vmap",
            "/vcharts/vsankey",
            "/vcharts/vheatmap",
            "/vcharts/vscatter",
            "/vcharts/vcandle",
            "/vcharts/vgauge",
