      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      }
    }
  }

</script>

<style scoped>
  .el-menu-vertical-demo:not(.el-menu--collapse) {
    width: 200px;
    min-height: 400px;

  }

  .el-menu {
    height: calc(100vh - 60px);
    background: #EBF1F6;

  }

  ul.el-menu--popup li.el-menu-item {
    line-height: 40px;
    height: 40px;
    border-bottom: 1px solid #f4f4f4;
  }

  ul.el-menu--popup {
    padding: 0 !important;
  }

  li.el-submenu>div.el-submenu_title {
    height: 40px !important;
    line-height: 40px !important;
  }

</style>



