<template>
  <el-container id="index">
    <el-header>
      <i @click="toggleList()" class="toggleList el-icon-menu" style="line-height:60px;"></i>
      <header-bar></header-bar>
      <div class="signout">
        <span>Welcome,{{ adminName }}</span>
        <i class="el-icon-erp-dengchu" @click="signout()" alt="注销"></i>
      </div>
    </el-header>
    <el-container>
      <el-aside>
        <radio-bar :isCollapse="isCollapse"></radio-bar>
      </el-aside>
      <el-main>
        <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
  import radioBar from '@/components/radioBar'
  import headerBar from '@/components/headerBar'

  export default {
    name: 'index',
    data() {
      return {
        adminName: sessionStorage.getItem('username'),
        isCollapse: true
      }
    },
    components: {
      radioBar,
      headerBar
    },
    methods: {
      //登出
      signout() {
        sessionStorage.removeItem('username');
        this.$router.push({
          'path': '/login'
        });
      },
      toggleList() {
        this.isCollapse = !this.isCollapse;
      }
    }
  }

</script>

<style>
  .el-header {
    color: #333;
    line-height: 60px;
  }

  #header_bar {
    background: #ebf1f6;
  }

  .el-aside {
    color: #333;
    min-height: calc(100vh - 60px);
    width: auto !important;
  }

  .el-main {
    color: #333;
  }

  body>.el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

  .el-menu-demo {
    width: auto;
    position: absolute;
    left: 200px;
  }

  .signout {
    position: absolute;
    top: 0;
    right: 0;
    width: auto;
    padding: 0 20px;
  }

  .toggleList {
    position: absolute;
    top: 0;
    left: 150px;
    line-height: 60px;
  }

  .signout i {
    margin-left: 20px;
  }

  .signout i,
  .toggleList {
    cursor: pointer;
  }

  .el-header,
  .el-menu-item,
  .el-submenu__title,
  .el-menu-item-group__title,
  .el-aside {
    background: #EBF1F6;
  }

</style>



